nav {
position: fixed;
top: 0;
width: 100%;
height: 50px;
background: linear-gradient(45deg, rgb(156,14, 156), midnightblue);
}
.liste {
    list-style-type: none;
    background: linear-gradient(45deg, rgb(156,14, 156), midnightblue);
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    font-size: 22px;
}
.liste a{
    text-decoration: none;
    color: whitesmoke;
}
.btn {
    display: none;  
    position: absolute;
    top: 14px;
    right: 10px;
    background: transparent;
    border: none;
  
}
.liste__item {
    margin:0 5px;
    text-transform: uppercase;
    color: whitesmoke;
    cursor: pointer;
}
img {
    width: 20px;
}

@media screen and (max-width: 500px) {
 
    .liste {
        flex-direction: column;
        height:auto;
    }

    .liste__item:nth-child(1) {
        border-top: 1px solid rgba(255, 255, 255, 0.6);
        margin-top: 50px;
    }
    .liste__item {
        margin-right: 0;
        height: 70px;
        width: 100%;
        border-bottom: 1px solid rgba(255, 255, 255, 0.6);
        line-height: 70px;
        text-align: center;
    }
    .btn{
        display: block;
    }
}